/* Estilo base do aplicativo */
.App {
  text-align: center;
  background-color: #f0f2f5;
}

/* Animação do logo */
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Cabeçalho do aplicativo */
.App-header {
  background-color: #20232a;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 20px;
}

/* Estilos para containers e flexbox */
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  padding: 20px;
  border-radius: 12px;
}

.button-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 60px;
}

.row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* Estilos para elementos específicos */
.circle {
  margin-left: 8px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: #61dafb;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  color: #20232a;
}

.logo {
  margin: 20px auto;
  width: auto;
  height: 90px;
  margin-bottom: 10vmin;
}

/* Botões e interações */
.button {
  background: #61dafb;
  color: #20232a;
  cursor: pointer;
  padding: 15px 30px;
  font-size: 18px;
  border: none;
  border-radius: 20px;
  transition: all 0.3s ease;
  margin-bottom: 3vmin;
}

.button:hover {
  transform: scale(1.05);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

.button:active {
  transform: scale(0.95);
}

.button:focus {
  outline: none;
  box-shadow: 0 0 0 2px #61dafb;
}

/* Estilos para texto e links */
.App-link {
  color: #61dafb;
}

.text {
  color: #f00202;
  margin: 0;
  padding: 0 10px;
  margin-bottom: 20px;
  margin-top: 8px;
  font-size: 20px;
  text-align: center;
}

.description {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 10px;
}

/* Estilos para inputs e opções */
.input, .option {
  background-color: #20232a;
  color: #ffffff;
  border: 1px solid #444444;
  padding: 10px;
  border-radius: 8px;
  margin: 0 10px;
  font-size: 16px;
}

.input {
  width: 400px;
  height: 26px;
}

.option {
  width: 180px;
  height: 50px;
}